/* eslint-disable react-hooks/exhaustive-deps */
import * as Yup from 'yup';
import {  useMemo } from 'react';
// form
import { useForm } from 'react-hook-form';
import { yupResolver } from '@hookform/resolvers/yup';
import { useNavigate } from 'react-router-dom';
// swal
import swal from 'sweetalert';
// @mui
import { LoadingButton } from '@mui/lab';
import { Box, Card, Grid, Alert, Stack} from '@mui/material';
import axios from '../../utils/axios';
import { FormProvider,   RHFTextField,  } from '../../components/hook-form';
// routes
import { PATH_ADMIN } from '../../routes/paths';
// drop dwon style
import useIsMountedRef from '../../hooks/useIsMountedRef';
// ----------------------------------------------------------------------

export default function HabukhanBlockUser() {
    const isMountedRef = useIsMountedRef();
  const NewUserSchema = Yup.object().shape({
   number: Yup.string().required('Block Number Required')
  });

  const AccessToken =  window.localStorage.getItem('accessToken');
  const navigate = useNavigate();
  const methods = useForm({
    resolver: yupResolver(NewUserSchema),
  });
  const defaultValues = useMemo(() => ({
     number: ''
    })
  );

  const {
    setError,
    handleSubmit,
    reset,
    formState: {errors, isSubmitting },
  } = methods;
 
  const onSubmit = async (data) => {
    try {
     await axios.post(`/api/reset/user/block/number/${AccessToken}/habukhan/secure`,data)
     swal('success', `Block Number Added Successfully`, 'success');
     navigate(PATH_ADMIN.user.bannednumber);
    } catch (error) {
        if (isMountedRef.current){
            setError('afterSubmit', error);
          }
    }
  };
  const onReload = async (data) => {
    try {
        await axios.post(`/api/reset/user/block/number/${AccessToken}/habukhan/secure`,data)
        reset(defaultValues);
        swal('success', `Block Number Added Successfully`, 'success');
      
       } catch (error) {
           if (isMountedRef.current){
               setError('afterSubmit', error);
             }
       }
  }
  return (
    <FormProvider methods={methods} onSubmit={handleSubmit(onSubmit)}>
      <Grid container spacing={3}>
     
        <Grid item xs={12} md={8}>
          <Card sx={{ p: 3 }}>
            <Box
              sx={{
                display: 'grid',
                columnGap: 1,
                rowGap: 3,
                gridTemplateColumns: { xs: 'repeat(1, 1fr)', sm: 'repeat(1, 1fr)' },
              }}
            >
                 {!!errors.afterSubmit && <Alert severity="error">{errors.afterSubmit.message}</Alert>}
                
              <RHFTextField name="number" label="Block Number"/>
            </Box>
            <Stack alignItems="flex-end" sx={{ mt: 2, display: 'grid',
                columnGap: 1,
                rowGap: 2,  gridTemplateColumns: { xs: 'repeat(1, 1fr)', sm: 'repeat(2, 1fr)' },}}>

              <LoadingButton  fullWidth size="large" type="submit" variant="contained" loading={isSubmitting}>
               Proceed then Return
              </LoadingButton>
              <LoadingButton fullWidth size="large" type="button" onClick={handleSubmit(onReload)} variant="contained" color="warning" loading={isSubmitting}>
                Proceed then Reset
              </LoadingButton>
            </Stack>
          </Card>
        </Grid>
      </Grid>
    </FormProvider>
  );
}
